@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");
html {
  font-size: 100%;
  box-sizing: border-box;
  text-decoration: none;
  padding: 0;
  margin: 0;
  font-family: "Poppins", sans-serif;
  line-height: 1.1;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

:root {
  --head-font: #20292f ;
  --head-bg: #fff;
  --head-hover: #1c2429;
  --head-border: rgba(32,41,47, 0.5) ;
}

/*@function rem($pixels, $context: 16) {
    @return (math.div($pixels, $context)) * 1rem;
}*/
.header {
  display: flex;
  max-width: 100%;
  padding: 0 2.5rem;
  justify-content: space-between;
  color: var(--head-font);
  font-display: swap;
  background-color: var(--head-bg);
  align-items: center;
}
.header__brandlogo__title {
  font-size: 2rem;
  font-weight: 600;
  margin: 0;
  padding-top: 1.6875rem;
}
.header__brandlogo__subtitle {
  font-weight: 400;
}
.header__navmenu {
  flex-direction: column;
  list-style-type: none;
}
@media (min-width: 40em) {
  .header__navmenu {
    display: flex;
    font-size: 0.9375rem;
    flex-direction: row;
  }
}
.header__navmenu__container {
  padding-right: 1.375rem;
  font-weight: 500;
}
.header__navmenu__container:hover {
  color: var(--head-hover);
}
.header__navmenu__link {
  text-decoration: none;
  color: inherit;
}
.header__navmenu #header__navmenu__toggle {
  display: none;
}
.header__navmenu__label {
  display: flex;
  height: 2.5rem;
  width: 2.5rem;
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.header__navmenu__button, .header__navmenu__button::before, .header__navmenu__button::after {
  display: block;
  background-color: var(--head-font);
  position: absolute;
  height: 0.25rem;
  width: 1.875rem;
  transition: transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
  border-radius: 0.125rem;
}
.header__navmenu__button::before {
  content: "";
  margin-top: -0.5rem;
}
.header__navmenu__button::after {
  content: "";
  margin-top: 0.5rem;
}
.header__navmenu__menu {
  position: absolute;
  top: 0;
  margin-top: 3.125rem;
  left: 0;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  list-style-type: none;
  padding: 0;
  display: none;
}
.header__navmenu #menu-header__navmenu__toggle ~ .header__navmenu__menu li {
  height: 0;
  margin: 0;
  padding: 0;
  border: 0;
  transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
}
.header__navmenu__menu > li {
  display: flex;
  justify-content: center;
  width: 100%;
}
.header__navmenu__menu {
  padding-top: 1.25rem;
}
.header #header__navmenu__toggle:checked + .header__navmenu__label .header__navmenu__button::before {
  margin-top: 0;
  transform: rotate(405deg);
}
.header #header__navmenu__toggle:checked + .header__navmenu__label .header__navmenu__button::after {
  margin-top: 0;
  transform: rotate(-405deg);
}
.header #header__navmenu__toggle:checked + .header__navmenu__label .header__navmenu__button {
  margin-top: 0;
  background-color: inherit;
}
.header #header__navmenu__toggle:checked ~ .header__navmenu__menu li {
  border-bottom: 0.00625rem solid var(--head-border);
  height: 2.5em;
  padding: 0.5em;
  transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
  width: 40%;
  background-color: rgba(255, 255, 255, 0.5);
}
.header #header__navmenu__toggle:checked ~ .header__navmenu__menu li:nth-child(1) {
  border-top: 0.00625rem solid var(--head-border);
}
.header #header__navmenu__toggle:checked ~ .header__navmenu__menu {
  display: flex;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    flex-direction: row;
  }
  .header__navmenu {
    display: flex;
    height: auto;
    width: auto;
  }
  .header__navmenu__label, .header__navmenu__button {
    display: none;
  }
  .header__navmenu__menu {
    position: relative;
    display: flex;
    flex-direction: row;
    width: auto;
  }
  .header .header__navmenu__menu {
    margin: 0;
    padding: 0;
  }
  .header .header__navmenu__menu li:hover {
    color: var(--head-hover);
  }
  .header #header__navmenu__toggle:checked ~ .header__navmenu__menu li {
    border-bottom: none;
  }
  .header #header__navmenu__toggle:checked ~ .header__navmenu__menu li:nth-child(1) {
    border-top: none;
  }
}

